import React, { useState, useEffect } from 'react';
import { useHistory } from "react-router";
import { List, ListItem, ListItemText } from '@material-ui/core/';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from "@material-ui/core/CardHeader";
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import StripeCheckout from 'react-stripe-checkout';
import { cartService } from '../../../store/Services';


const logo = "data:image/png;base64,";

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
  },
  card: {
    marginBottom: '15px'
  },
  headerColor: {
    background: '#efeff5'
  },
  button: {
    width: '100%',
    background: '#459cb3'
  }
}));

const Confirm = (props) => {
  const { values: { name, phone, email, street, district, city, country, zipcode, state } } = props;
  const [stripeToken, setStripeToken] = useState(null);
  const classes = useStyles();
  const history = useHistory();

  const continues = e => {
    e.preventDefault();
    props.nextStep();
  };

  const back = e => {
    e.preventDefault();
    props.prevStep();
  };

  const onToken = async (token) => {
    setStripeToken(token);
  }

  useEffect(() => {
    const makeRequest = async () => {
      try {
        const res = await cartService.checkout({ tokenId: stripeToken.id, amount: 2000, })
        console.log(res);
        history.push("/success");
      } catch (err) {
        console.log(err);
      }
    }
    stripeToken && makeRequest();
  }, [stripeToken, history])

  return (
    <div>
      {stripeToken ? (<span>Processing... Please wait...</span>) : (
        <Card className={classes.card}>
          <CardHeader className={classes.headerColor} title="Confirm & Payment" />
          <CardContent>
            <Grid container spacing={3}>
              <Grid item lg={12} sm={9} xl={6} xs={3}>
                <h2>User Details</h2>
                <List>
                  <ListItem>
                    <ListItemText primary="Name" secondary={name} />
                  </ListItem>
                  <ListItem>
                    <ListItemText primary="Phone" secondary={phone} />
                  </ListItem>
                  <ListItem>
                    <ListItemText primary="Email" secondary={email} />
                  </ListItem>
                </List>
              </Grid>
              <Grid item lg={12} sm={9} xl={6} xs={3}>
                <h2>Shipping Details</h2>
                <List>
                  <ListItem>
                    <ListItemText primary="Street" secondary={street} />
                  </ListItem>
                  <ListItem>
                    <ListItemText primary="City" secondary={city} />
                  </ListItem>
                  <ListItem>
                    <ListItemText primary="District" secondary={district} />
                  </ListItem>
                  <ListItem>
                    <ListItemText primary="State" secondary={state} />
                  </ListItem>
                  <ListItem>
                    <ListItemText primary="Country" secondary={country} />
                  </ListItem>
                  <ListItem>
                    <ListItemText primary="Zipcode" secondary={zipcode} />
                  </ListItem>
                </List>
              </Grid>
            </Grid>
            <Button
              color="secondary"
              variant="contained"
              onClick={back}
            >Back</Button>

            <StripeCheckout
              amount="500"
              billingAddress
              description="Awesome Product"
              image={logo}
              locale="auto"
              name="MernPress.com"
              stripeKey={process.env.REACT_APP_STRIPE_KEY}
              token={onToken}
              zipCode
              panelLabel="Rent for {{amount}}"
            />
          </CardContent>
        </Card>
      )}
    </div>
  );
}

export default Confirm;
